import React from "react";
import Link from "next/link";

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from "reactstrap";

class NavbarClass extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    return (
      <div>
        <Navbar color="white" light expand="md" className="shadow bg-danger">
          <NavbarBrand href="/">
            <img src="/static/images/logo.png" width="150" height="90" />
          </NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <NavLink>
                  <i className="fas fa-home ml-2"></i>
                  <Link href="/">
                    <a href="#" className="text-secondary">
                      صفحه اصلی
                    </a>
                  </Link>
                </NavLink>
              </NavItem>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  <i className="fas fa-info-circle ml-2"></i>
                  درباره ما
                </DropdownToggle>
                <DropdownMenu right className="shadow rounded">
                  <DropdownItem>
                    <Link href="/about/1">
                      <a href="/about/1">چشم انداز مدیریت</a>
                    </Link>
                  </DropdownItem>
                  <DropdownItem>
                    <Link href="/about/2">
                      <a href="/about/2"> چارت سازمانی</a>
                    </Link>
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>
                    <Link href="/about/3">
                      <a href="/about/3">پیام مدیرعامل</a>
                    </Link>
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>

              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  <i className="fas fa-plane ml-2"></i>
                  ناوگان هوایی
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem>بوئینگ 737</DropdownItem>
                  <DropdownItem>ایرباس 320</DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>

              <NavItem>
                <NavLink href="/magazine">
                  <i className="fas fa-newspaper ml-2"></i>
                  <span>مجله گردشگری</span>
                </NavLink>
              </NavItem>

              <NavItem>
                <NavLink href="/news">
                  <i className="fas fa-rss ml-2"></i>
                  <span>اخبار</span>
                </NavLink>
              </NavItem>

              <NavItem>
                <NavLink href="/coorporate">
                  <i className="fas fa-handshake ml-2"></i>
                  <span>همکاری</span>
                </NavLink>
              </NavItem>

              <NavItem>
                <NavLink href="/contact">
                  <i className="fas fa-phone ml-2"></i>
                  <span>تماس با ما</span>
                </NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}

export default NavbarClass;
